.hero-insular {
    width: 47%; }

.insular-title {
    font-size: 3.5em;
    margin-bottom: 0;
    margin-top: 10px; }

.esfera-1 {
    width: 20%;
    float: right;
    margin-bottom: -14%;
    z-index: 2;
    position: relative;
    mix-blend-mode: multiply; }

.esfera-2 {
    width: 20%;
    margin-top: -10%;
    mix-blend-mode: multiply; }


a {
    text-decoration: none;
    color: inherit; }

a:hover {
    color: inherit; }


.fuso-insular-banner:hover {
    background-color: white; }

@media only screen and (min-width: 600px) {
    .desktop {
        display: block; }

    .mobile {
        display: none; } }

@media only screen and (max-width: 600px) {
    .desktop {
        display: none !important; }

    .mobile {
        display: block !important; }

    .hero-insular {
        width: 100%; }

    .insular-title {
        font-size: 1.5em; }

    .esfera-insular-mobile {
        width: 35%;
        margin-bottom: -5px; }

    .font-larger {
        font-size: 1.5em; }

    .mobile-45-logo {
        height: 44vh; }

    .esfera-1 {
        width: 35%;
        margin-bottom: -30%;
        float: left; }

    .fuso-index-dates {
        margin-top: -18%; }

    .fuso-banner {
        height: auto; }

    .fuso-insular-banner {
        padding-bottom: 15vh !important; } }
